	font-weight: bold;
}
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: TrendSansOne;
}

.Header ul {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 0;
    grid-column-gap: 15px;
    -webkit-column-gap: 15px;
            column-gap: 15px;
}
.Header {
    background-color: #f0ebe7;
}
.Header a {
    font-size: 15px;
    color: #4c4557;
    text-decoration: none;
}
.Header a:hover{
    color: #000;
}
.Header a li{
    list-style: none;
}
.Header .ni{
    border-radius: 20px;
    padding: 10px;
    transition-duration: 0.2s;
}
.Header .ni:hover{
    background-color: #0000001c;
    transition-duration: 0.2s;
}
.cs-item{
    background-repeat: no-repeat;
    min-height: 500px;
    width: 100%;
}
.c-slider {
    width: 300%;
    overflow: hidden;
    display: flex;
    transition-duration: 0.5s;
}
.carousel-wrapper{
    width: 100%;
    margin: auto;
    overflow: hidden;
    max-width: 1600px;
}
.cb-item {
    cursor: pointer;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    margin: auto;
    border: 1px solid #000;
    transition-duration: 0.5s;
}
.cb-item-active{
    transition-duration: 0.5s;
    background-color: #000;
}
.c-btn-wrapper {
    padding: 10px 0;
    display: flex;
    grid-column-gap: 10px;
    -webkit-column-gap: 10px;
            column-gap: 10px;
    max-width: 50px;
    margin: auto;
}
